.headerNavbar {
    height: 60px;
    /*width: 100%;*/
    background: #424242 none repeat scroll 0 0;
    display: flex;
    align-items: center;
    padding: 0 43px;
    color: white;

}

.headerNavbar_ava {

    border: 5px solid rgba(255, 255, 255, 0.99);
    border-radius: 100%;
    box-shadow: 0 1px 0 #e1e8ed;
    /*float: right;*/
    /*margin-top: -138px;*/
    overflow: hidden;
    /*position: relative;*/
    /*width: auto;*/

    width: 170px;
    height: 170px;
    /*height: auto;*/
    position: absolute;
    bottom: 30px;
}
.headerNavbar_ava  {
    background-image: url('./HeaderNav_Img/navAva1.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}
.plug {
    margin-right: 50px;
    width: 170px;
    height: auto;
}
.headerNavbar_ul {
    display: inline-block;
    list-style: outside none none;
    margin-bottom: 0;
    padding-left: 20px;
    vertical-align: middle;
    width: 100%;
}

ul {
    margin: 0;
    padding: 0;
}
li {
    cursor: pointer;
}
.headerNavbar_ul li {
    display: inline-block;
    margin-right: 50px;
    position: relative;
    vertical-align: middle;
}

.headerNavbar_admin {
    position: relative;
}
h5 {
    margin: 0;
    padding: 0;
}
.headerNavbar_admin > h5 {
    color: #088dcd;
    display: inline-block;
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 0;
    vertical-align: super;
}
.headerNavbar_admin span {
    bottom: 0px;
    font-size: 10px;
    line-height: 5px;
    position: absolute;
    right: 0;
}
.headerNavbar_links {
    display: flex;

}
